.App {
  text-align: center;
}

* {
  box-sizing: border-box;
}

header {
  width: 100%;
  height: 50px;
  border-bottom: 2px solid hsl(210deg 18% 87%);

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

/* Components CSS start */
:root {
  --blue: #1e90ff;
  --orange: #e34c26;
  --voilet: #CD6799;
  --yellow: #f0db4f;
  --purple: #563d7c;
  --Darkblue: #61DBFB;
}

.container {
  display: flex;
  background: rgb(172, 154, 197);
  width: 100%;
  align-items: center;
}

.heading {
  display: block;
  text-align: center;
  font-size: 25px;
  font-weight: 600;
  color: whitesmoke;
  background: rgb(172, 154, 197);
}

.sub-heading-1 {
  width: 450px;
  margin-left: 25px;

}

.sub-heading-2 {
  width: 401px;
  margin-left: 49px;
}

.img-container>img {
  max-width: 100%;
}


.container .item-1 {
  width: 770px;
  background-color: hsl(210deg 18% 87%);
  max-width: 100%;
  padding: 40px;
}

/* nav bar CSS */
.header-container {
  max-width: 100%;

}

.content p a {
  text-decoration: none;
  color: hsl(0deg 0% 70%);
  font-weight: 500;
}

.header-container nav {
  align-items: center;
  justify-content: start;
  margin-top: -57px;
}

.header-container nav ul {
  display: flex;
  list-style: none;
  gap: 30px;
  justify-content: flex-start;
  margin: 0;
  padding-top: 20px;
}

.header-container nav ul li {
  padding-bottom: 13px;
  opacity: 0.5;
}

.header-container nav ul li:hover {
  cursor: pointer;
  border-bottom: 3px solid hsl(210deg 18% 87%);
  padding-bottom: 13px;
  opacity: 1.5 !important;

}

.active {
  border-bottom: 1px solid hsl(210deg 18% 87%);
  cursor: pointer;
  font-weight: bolder;
  opacity: 1.5 !important;
  border-bottom: 3px solid#fd8c73;
}

/* Component Userprofile CSS start */
button:hover {
  cursor: pointer;
}

.user-name {
  color: #0969da;
  font-weight: 600;
  margin-left: 2px;
  margin-right: 2px;
}

.img-container {
  position: relative;
  text-align: left;
  margin-left: 33px;
  margin-top: 36px;
  margin-right: 33px;
}

.section-1 {
  max-width: 100%;
  width: 300px;
  margin-top: -65px;
  display: block;
  /* margin-right: 30px; */
  position: relative;
}

.counter {
  display: flex;
  align-items: center;
  width: 500px;
  height: 130px;
  background-color: #2b2b9d;
  margin: 0 auto;
  justify-content: center;
  gap: 5px;
}

.section-1 .img-container img {
  border-radius: 50%;

}

.smile-face {
  width: 28px;
  background: #ffffff;
  border: 1px solid hsl(210deg 18% 87%);
  border-radius: 60%;
  height: 29px;
  text-align: center;
  position: absolute;
  left: 81%;
  top: 38%;
  z-index: 99;
}

.fa-solid,
.fas {
  font-size: 15px;

}

.custom-smily {
  margin-top: 7px;
  margin-left: 0px;
}

.content h4 {
  margin-top: 0px;
  margin-bottom: 0px;
  font-size: 27px;
  font-weight: 400;
}

.content>button {
  border: 2px solid rgba(27, 31, 36, 0.15);
  border-radius: 7px;
  font-size: 15px;
  width: 296px;
  padding: 6px;
  background-color: #f6f8fa;
}

.light-text {
  opacity: 0.5;
}

.content:nth-child(3) {
  font-size: 18px;
  max-width: 100%;
  width: 330px;
  margin-bottom: 10px;
  text-align: left;
}

.content>p {
  margin-top: 0px;
  font-size: 16px;
}

.following {
  display: flex;
  align-items: center;
  gap: 7px;
  margin-left: 16px;

}

/* Aboutuser Component CSS   */
.discription-container {
  max-width: 100%;
  width: 850px;
  padding: 25px;
  border: 1px solid hsl(210deg 18% 87%);
  margin-top: 0px;
  margin-top: 25px;
  border-radius: 10px;
}

.main_content {
  padding-top: 30px;
}

.item_2>p {
  font-size: 14px;
  line-height: 2.0;
}

.item_2 a {
  text-decoration: none;
  color: dodgerblue;
  font-size: 18px;
  font-weight: 500;
}

.common_flex-box {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.left-border {
  border-left: 4px solid #8c959f;
  padding-left: 9px;
}

.item_1 span {
  display: flex;
  gap: 10px;
}

.item_2 {
  text-align: left;
}

.item_1 span button {
  border: 2px solid hsl(210deg 18% 87%);
  border-radius: 7px;
  color: dodgerblue;
  padding: 5px;
  background-color: #f6f8fa;
}

.item_2 h3 p {
  margin-bottom: 10x;
  margin-top: 10px;
}

.tabs {
  display: none;
}

.raise-hand-emoji {
  max-width: 100%;
  width: 20px;
}

/* Nav Bar CSS */
.origin {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.Empty-container {
  max-width: 100%;
  margin-left: 170px;
}

/* CSS TAB4 */
.Skill-container {
  display: flex;
  align-items: center;
  justify-content: space-around;

}

.Skill-container>div {
  padding: 20px;
  border: 1px solid hsl(210deg 18% 87%);
  background-color: #f6f8fa;
}

/* skill tab css */
.custom-fa-html5 {
  color: var(--orange);
  font-size: 68px;
}

.custom-fa-css3 {
  color: var(--blue);
  font-size: 68px;
}

.custom-fa-square-js {
  color: var(--yellow);
  font-size: 68px;
}

.custom-fa-sass {
  color: var(--voilet);
  font-size: 68px;
}

.custom-fa-react {
  color: var(--Darkblue);
  font-size: 68px;
}

.custom-fa-bootstrap {
  color: var(--purple);
  font-size: 68px;
}

/* tab4 css */
.social-handel {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 10px;
}

.contact-info h2 {
  text-decoration: none;
  color: dodgerblue;
  font-size: 18px;
  font-weight: 500;
}

/* Media Query */

@media only screen and (max-width:1024px) {
  .discription-container {
    width: 610px;
  }
}

@media only screen and (max-width:768px) {
  .discription-container {
    width: 410px;
  }
}

/* Mobile Screen Media Query */
@media only screen and (max-width: 425px) and (min-width:320px) {
  .origin {
    flex-wrap: wrap;
  }

  .section-1 {
    margin-top: 0px;
    margin-left: 0;
  }

  .discription-container {
    width: 350px;
  }

  .content h4 {
    font-size: 24px;
  }

  .img-container {
    margin-top: 16px;
  }

  header {
    display: none;
  }

  .header-container nav ul {
    padding-top: 45px;
    justify-content: unset;
  }

  .Skill-container {
    justify-content: center;
    flex-wrap: wrap;
  }

  .following {
    margin-left: 0;
  }


}

